.App {
  text-align: center;
  // background-color: #dfcef7;
  background: #e0dbff;
  font-family: "Josefin Sans", sans-serif;
  color: #033a49;
  position: relative;
}

#preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999999;
}

html, body {
  height: 100%;
}

#home {
  background: url(/server/public/wave-haikei-1500x600-6f0f37xe0dbff.svg);
  // background: url(/server/public/wave-haikei-average-berry-background-top.svg);
  // background-size: cover;
  // background: #fbcaa0;
  padding-top: 200px;
  padding-bottom: 250px;
  // color: #dfcef7;
  color: #e0dbff;
}

body {
  font: 20px 'Josefin Sans', sans-serif;
  line-height: 1.8;
  // color: #fff5f0;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.margin {
  margin-bottom: 20px;
}

p { 
  font-size: 16px;
  margin: 0;
  padding: 30px;
}


@media (min-width: 600px) {
  section {
    -webkit-flex-direction: column;
    flex-direction: column;
    // padding: 60px;
  }
  p {
    font-size: 1.2vw;
  }
}

@media (max-width: 600px) {
  #profile-img {
    width: 80%;
  }
  #home {
    padding-bottom: 150px; 
  }
}

.fa { 
  margin: 10px;
  padding: 20px;
  font-size: 30px;
  text-align: center;
  text-decoration: none;
  color: #033a49;
  border-radius: 100%;
  border: 2px #033a49 solid; 
}

.fa:hover {
  color: #00a2e2;
  opacity: 0.9;
  border-color: #00a2e2;
  box-shadow: 5px 10px 10px #3e87f562;
}
